export const sourceCode = "import { Cards } from 'nextra/components'\nimport {BoxCard} from '../components/BoxCard'\nimport Link from 'next/link'\nimport Image from \"next/image\"\n\n# Get Started with Boxicons\n\n<Image className=\"file-icon\" src=\"/file.svg\" alt=\"file\" width={150} height={150} />\n\n\nGetting started with Boxicons is simple and flexible, whether you are a designer working in Figma or a developer building a web or app interface. This guide walks you through the essential steps to integrate Boxicons into your workflow and start using icons right away. Youll learn how to install Boxicons using different methods, use the Figma plugin, import icons into your code, and customize their style, weight, and appearance. Whether youre prototyping, building a design system, or shipping a production app, Boxicons helps you stay consistent, fast, and visually polished across all your projects.\n\n<Link className=\"main-button\" href=\"/introduction\">Let's Go</Link>\n\n---\n<br/>\n<Cards >\n<BoxCard\n    icon={<i className=\"bxdocs bxr-200-cursor-pen bx-md absolute right-5 top-5 opacity-50\" />}\n    title=\"Fonts\"\n    href=\"/font/usage\"\n    description=\"Embed the icons directly into your project using icon fonts for fast and easy integration.\"\n  /><BoxCard\n    icon={<i className=\"bxdocs bx-figma bx-md absolute right-5 top-5 opacity-50\" />}\n    title=\"Figma Plugin\"\n    href=\"/figma/install\"\n    description=\"Browse, search, and insert Boxicons directly into your designs using the Figma plugin.\"\n  /><BoxCard\n    icon={<i className=\"bxdocs bx-react bx-md absolute right-5 top-5 opacity-50\" />}\n    title=\"React Package\"\n    href=\"/react/install\"\n    description=\"In Testing Phase. Use Boxicons as tree-shakable React components for a fast and modern development workflow.\"\n  />\n</Cards>";
